@import url('https://fonts.googleapis.com/css2?family=Alegreya&family=Lato:wght@300&display=swap');
// font-family: 'Lato', sans-serif;
// font-family: 'Alegreya', serif;

.app-init {
    .selected {
        background: #f5821c;
      }
    
      
    
      .white-card {
        background: rgb(136, 50, 50);
        padding: 1rem;
      }
    
      .big-icon {
        font-size: 6rem;
        color: $medium-color;
      }
    
      .query-section {
        background: $secondary-color;
        color: $white-color;
        padding: 1rem;
        display: flex;
        justify-content: space-around;
        align-items: center;

        p {
            color: $medium-color;
        }
      }
      
      
      .tech-section {
        background: white;
        padding: 3rem 0;
        .tech-stack-container {
          display: flex;
          flex-wrap: wrap;
  
          .techy {
            background: #f5821f;
            margin: 5px;
            padding: 5px 10px;
            border-radius: 5px;
            font-size: 14px;
            color: black;
          }


          @for $i from 1 through 3 {
            .techy:nth-child(3n + #{$i}) {
              background-color: lighten($medium-color, $i * 5%);
            }
          }

        }
      }
      
    
}
