.app-header {
  background: $light-color;
  display: flex;
  align-items: center;
  justify-content: space-between;

  .logo-wrapper {
    img {
      width: 100%;
      max-width: 200px;
    }
  }

  .custom-navigation {
    ul {
      display: flex;
      list-style-type: none;
      flex-wrap: wrap;
      padding: 0;

      li {
        .active {
          color: $primary-color;
        }
        a {
          padding: 1rem;
          margin-right: 2px;
          text-transform: capitalize;
          font-size: 17px;
          font-weight: 500;
          color: $medium-color-darken;
        }
      }
    }
  }
}

.sub-header {
  background: $light-color-lighten;
  padding: 1rem;
  margin-bottom: 1rem;
}