.content-holder {

    .landing-section {
        padding: 4rem 2rem;
        background: $white-color;
    }

    
    .page-home-categories {
        background: #fafafa;
        padding-bottom: 100px;

        .page-home-categories-list {
            margin-top: 60px;

            .page-home-categories-list-item-outer {
                background-color: #fff;
                padding: 2rem;
                height: 100%;
                box-sizing: border-box;
                position: relative;
                border-radius: 10px;
            }
        }
    }      
     
}