.app-init {
    .MuiButton-containedPrimary,
    .MuiChip-colorPrimary {
        background-color: $primary-color;

        &:hover {
            background-color: $primary-color-darken;
        }
    }

    .MuiBreadcrumbs-ol {
        label {
            margin-bottom: 0;
        }
    }


    .MuiTypography-h3,
    .MuiTypography-body1,
    p,
    a {
        color: $secondary-color;
    }

    a:hover {
        text-decoration: none;
    }

    .MuiTypography-h1 {
        font-size: 4rem;
    }
}
